<template>
  <div class="post" ref="postRef">
    <el-breadcrumb separator="">
      <el-breadcrumb-item class="gender">
        <div v-if="item.senderSex === 1"><i class="fas fa-mars" style="color: #3B6FA8"></i></div>
        <div v-else-if="item.senderSex === 2"><i class="fas fa-venus" style="color: deeppink"></i></div>
        <div v-else><i class="fas fa-genderless"></i></div>
        <div class="name" @click="goSearch(item.sender)">
          {{ item.sender }}
        </div>
      </el-breadcrumb-item>
      <el-breadcrumb-item class="gender">
        <div class="name" @click="goSearch(item.recipient)">
          {{ item.recipient }}
        </div>
        <div v-if="item.recipientSex === 1"><i class="fas fa-mars" style="color: #3B6FA8"></i></div>
        <div v-else-if="item.recipientSex === 2"><i class="fas fa-venus" style="color: deeppink"></i></div>
        <div v-else><i class="fas fa-genderless"></i></div>
      </el-breadcrumb-item>
    </el-breadcrumb>

    <div @click="goTableDetail(item.id)" class="content-box" v-if="!isDetail">
      <div class="item-content">
        {{ item.content }}
      </div>
      <el-image
        style="width: 100px; height: 100px"
        v-for="(item,index) in imgList"
        :key="index"
        :src="item"
        fit="cover"
      >
      </el-image>
    </div>
    <div @click="goTableDetail(item.id)" class="content-box" v-else>
      <div class="item-content">
        {{ item.content }}
      </div>
      <el-image
        style="width: 222px; height: 222px"
        v-for="(item,index) in imgList"
        :key="index"
        :src="item"
        fit="cover"
        :preview-src-list="imgList"
      >
      </el-image>
    </div>

    <div class="info">
      <div v-if="item.anonymous === true || item.userPublic === undefined" class="user">
        <span style="color: #999;font-weight: bold">[]</span>
      </div>
      <div v-else class="user">{{ item.userPublic.name }}</div>
      <div class="create-time">{{ this.toDates(item.createTime) }}</div>
    </div>

    <el-button-group>
      <!--      -->
      <el-button
        icon="fas fa-thumbs-up"
        @click="thumbs_up"
        id="thumbsTrue"
        v-if="!item.support">
        {{ item.supportCount }}
      </el-button>
      <el-button
        icon="fas fa-thumbs-up"
        @click="un_thumbs_up"
        id="thumbsFalse"
        v-else>
        {{ item.supportCount }}
      </el-button>
      <!--    -->
      <el-button v-if="isDetail===false" icon="fas fa-comment-alt" @click="goTableDetail">
        {{ item.commentCount }}
      </el-button>
      <!--    -->
      <el-button icon="fas fa-share-square" @click="showQrCode"></el-button>
    </el-button-group>
    <!--    -->
    <el-dialog
      :visible.sync="QRVisible"
      width="37%">
      <div class="qrcode" ref="qrCodeUrl"></div>
      <div class="search" style="width: 32.125rem;margin-left: 1.25rem">
        <el-breadcrumb separator="" style="border-bottom: 0;font-size: 1.5em">
          <el-breadcrumb-item class="gender">
            <div v-if="item.sender_sex === 1"><i class="fas fa-mars" style="color: #3B6FA8"></i></div>
            <div v-else-if="item.sender_sex === 2"><i class="fas fa-venus" style="color: deeppink"></i></div>
            <div v-else><i class="fas fa-genderless"></i></div>
            <div class="name" @click="goSearch(item.sender)">
              {{ item.sender }}
            </div>
          </el-breadcrumb-item>
          <el-breadcrumb-item class="gender">
            <div class="name" @click="goSearch(item.recipient)">
              {{ item.recipient }}
            </div>
            <div v-if="item.recipient_sex === 1"><i class="fas fa-mars" style="color: #3B6FA8"></i></div>
            <div v-else-if="item.recipient_sex === 2"><i class="fas fa-venus" style="color: deeppink"></i></div>
            <div v-else><i class="fas fa-genderless"></i></div>
          </el-breadcrumb-item>
        </el-breadcrumb>
        <el-input style="height: 0.6rem" size="small" v-model="url" ref="urlText" readonly>
          <el-button slot="append" @click="copyUrl"></el-button>
        </el-input>
      </div>
    </el-dialog>
  </div>

</template>

<script>
  import QRCode from 'qrcodejs2'

  export default {
    data() {
      return {
        imgVisible: false,
        imagePath: axios.defaults.baseURL + '/api/file/image/',
        QRVisible: false,
        TableId: '',
        url: '',
        flag: false,
        CommentTotal: '',
        //  
        imgList: []
      }
    },
    name: 'Table',

    //item 
    //isDetail 
    //          
    props: ['item', 'isDetail', 'id'],
    methods: {
      // 
      async thumbs_up() {
        await this.$http.put('/api/table/support', {
          tableId: this.item.id
        }).then((data) => {
          this.$message.success(data.message)
          this.getTableData()
        })
      },
      // 
      async un_thumbs_up() {
        await this.$http.delete('/api/table/support', {
          tableId: this.item.id
        }).then((data) => {
          this.$message.success(data.message);
          this.getTableData()
        })
      },
      //id
      async getTableData() {
        await this.$http.get('/api/table/table', {
          id: this.item.id
        }).then((data) => {
          this.item = data.data
        })
      },
      // 
      getImgList() {
        if (!this.item.images) {
          return
        }
        // json
        let imgSrc = JSON.parse(this.item.images);

        for (let i = 0; i < imgSrc.length; i++) {
          this.imgList.push(this.imagePath + imgSrc[i]);
        }
      },
      // 
      goTableDetail() {
        if (this.isDetail)
          return;
        this.$router.push({
          name: 'TableDetail',
          params: {'id': this.item.id}
        })
      },
      //
      goSearch(keyword) {
        this.$router.push({
          name: 'Search',
          params: {keyword: keyword}
        })
      },
      // 
      showQrCode() {
        this.QRVisible = true
        this.TableId = this.item.id
      },
      // 
      creatQrCode() {
        this.$nextTick(() => {
          // url
          var origin = window.location.origin
          const url = origin + '/#/TableDetail/' + this.TableId
          this.url = url
          this.$refs.qrCodeUrl.innerHTML = '' //
          var qrcode = new QRCode(this.$refs.qrCodeUrl, {
            text: url, // 
            width: 100,
            height: 100,
            colorDark: '#0099ce',
            colorLight: '#ffffff',
            correctLevel: QRCode.CorrectLevel.H
          })
        })
      },
      copyUrl() {
        var ele = this.$refs.urlText
        ele.select()
        document.execCommand('Copy')
        this.$message.success('')
      }
    },
    created() {
      this.getImgList()
    },
    watch: {
      QRVisible: function (val, oldVal) {
        if (val === true) {
          this.creatQrCode()
        }
      },
      item: function (val, oldVal) {
        this.getImgList()
      }
    }
  }
</script>

<style scoped>
  /deep/ .el-breadcrumb__separator {
    color: red;
  }

  .item-content {
    min-height: 60px;
    padding-bottom: 10px;
    margin-bottom: 20px;
  }


  .image-box {
    display: inline-block;
  }

  .el-breadcrumb {
    display: flex;
    justify-content: center;
    padding-bottom: 1.25rem;
    border-bottom: 0.05rem solid #999;
  }

  .el-image {
    margin: 2px;
  }

  /*  */
  .post {
    height: auto;
    background-color: #fff;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0 0 1px #000 inset;
    display: block;
  }

  .post:last-child {
    margin-bottom: 0.6rem;
  }

  /*  */
  .content-box {
    padding: 0.8rem;
    margin-top: 1.25rem;
    width: 100%;
    height: 45%;
    word-wrap: break-word;
    box-shadow: 1px 1px 5px #CCCCCC70;
  }

  /*  */
  .el-button-group {
    display: flex;
    justify-content: center;
  }

  .info {
    display: flex;
    justify-content: flex-start;
  }

  /*  */
  .user {
    display: inline-block;
    color: #ff8686;
    font-size: 0.8rem;
    margin-top: 1.25rem;
    margin-bottom: 1.25rem;
    margin-left: 0.6rem;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  }

  /*  */
  .create-time {
    color: #999;
    font-size: 0.8rem;
    margin: 1.25rem 0.6rem 1.25rem auto;
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
  }

  .gender div {
    display: inline-block;
    vertical-align: -0.187rem;
  }

  .gender div i {
    font-size: 1.25rem;
  }

  /deep/ .el-dialog__body {
    display: flex;
    justify-content: center;
  }

  .name {
    font-weight: bold;
    color: #303133;
    cursor: pointer;

    display: inline-block;
    margin-left: 0.6rem;
    margin-right: 0.6rem;
  }

  #thumbsFalse {
    color: #409EFF;
    border-color: #c6e2ff;
    background-color: #ecf5ff;
  }

  #thumbsFalse:hover {
    color: #409EFF;
    border-color: #c6e2ff;
    background-color: #ecf5ff;
  }

  #thumbsTrue:focus {
    color: #606266;
    border-color: #DCDFE6;
    background-color: #FFF;
  }

  #thumbsTrue:hover {
    color: #409EFF;
    border-color: #c6e2ff;
    background-color: #ecf5ff;
  }
</style>
