<template>
  <div>
    <el-image class="range-img" :style="{transform:'rotate('+rotateAngle + 'deg)',transition:transition}" :src="imgUrl"
              fit="cover"></el-image>

    <div class="range" :class="rangeStatus?'success':''">
      <i @mousedown="rangeMove" :class="rangeStatus?successIcon:startIcon"></i>
      {{rangeStatus?successText:startText}}
    </div>
  </div>
</template>

<script>
  export default {
    props:
      {
        imgUrl: ["img-url"],
        // 
        mouseupFun: {
          type: Function
        },
        //
        successIcon: {
          type: String,
          default: 'el-icon-success'
        },
        //
        successText: {
          type: String,
          default: ''
        },
        //
        startIcon: {
          type: String,
          default: 'el-icon-d-arrow-right'
        },
        //
        startText: {
          type: String,
          default: ''
        },
        //
        errorFun: {
          type: Function
        },
        //
        status: {
          type: String
        },
      },
    data() {
      return {
        disX: 0,
        rotateAngle: 0,
        transition: '0s all',
        //
        rangeStatus: false
      }
    },
    watch: {
      status: function (val) {
        if(val === false) this.rangeStatus = false;
        this.rangeStatus = true;
        this.status = false

      }
    },
    methods: {
      //
      rangeMove(e) {
        let ele = e.target;
        //
        let startX = e.clientX;
        //
        let eleWidth = ele.offsetWidth;
        //
        let parentWidth = ele.parentElement.offsetWidth;
        //
        let MaxX = parentWidth - eleWidth;
        if (this.rangeStatus) {//
          return false;
        }
        // 
        document.onmousemove = (e) => {
          this.transition = '0s all';

          //
          let endX = e.clientX;
          //
          this.disX = endX - startX;
          if (this.disX <= 0) {
            this.disX = 0;
          }

          // if(this.disX>=MaxX-eleWidth/2){//1/2,
          //   this.disX = MaxX;
          // }
          if (this.disX >= MaxX) {//1/2,
            this.disX = MaxX;
          }
          ele.style.transition = '0s all';//
          ele.style.transform = 'translateX(' + this.disX + 'px)';
          e.preventDefault();
          //
          let everyAngle = 360 / MaxX;
          this.rotateAngle = this.disX * everyAngle;
        };
        //
        document.onmouseup = () => {
          //
          this.$emit('rotateAngle', this.rotateAngle);
          this.mouseupFun();
          this.ele = ele;
          this.disX = 0;
          ele.style.transition = '.5s all';//
          ele.style.transform = 'translateX(' + this.disX + 'px)';
          this.rotateAngle = 0;
          this.transition = '.5s all';


          //
          document.onmousemove = null;
          document.onmouseup = null;
        }
      }
    }
  };
</script>

<style scoped>
  .range {
    background-color: #FFCCCC;
    position: relative;
    transition: 1s all;
    user-select: none;
    color: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45px; /*no*/
    border-radius: 10px;
  }

  .range i {
    position: absolute;
    left: 0;
    width: 60px; /*no*/
    height: 100%;
    color: #919191;
    background-color: #fff;
    border: 1px solid #bbb;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;

  }

  .range.success {
    background-color: #7AC23C;
    color: #fff;
  }

  .range.success i {
    color: #7AC23C;
  }

  .range-img {
    width: 150px;
    height: 150px;
    text-align: center;
  }
</style>

