<template>
  <div class="login_container" v-title data-title="  ">
    <div class="content">
      <div class="title"></div>
      <div class="login_box">
        <el-form ref="RegisterFromRef" :model="RegisterFrom" :rules="RegisterFromRules" class="e_form">
          <!--        -->
          <el-form-item prop="email">
            <el-input v-model="RegisterFrom.email" prefix-icon="fas fa-lock"
                      placeholder=""></el-input>
          </el-form-item>
          <!--        -->
          <el-form-item prop="userName">
            <el-input v-focus v-model="RegisterFrom.userName" prefix-icon="fas fa-user"
                      placeholder=""></el-input>
          </el-form-item>
          <!--        -->
          <el-form-item prop="password">
            <el-input type="password" v-model="RegisterFrom.password" prefix-icon="fas fa-lock"
                      placeholder=""></el-input>
          </el-form-item>
          <!--        -->
          <el-form-item prop="passwordAgain">
            <el-input type="password" v-model="RegisterFrom.passwordAgain" prefix-icon="fas fa-lock"
                      placeholder=""></el-input>
          </el-form-item>
          <!--        -->
          <el-form-item class="btns">
            <!--            -->
            <el-popover
              placement="top"
              width="400"
              trigger="manual"
              v-model="showPopper">
              <div class="popover-container" v-if="showRotateCode === true">
                <div class="popover-close" @click="showPopper = false">+</div>
                <div class="popover-title">
                  <div class="popover-title-top">
                    {{popoverTitle}}
                  </div>
                </div>
                <Slider :mouseupFun="getEmailCode" :status="rangeStatus" :img-url="rotateCodeUrl"
                        @rotateAngle='(data)=> rotateAngle = parseInt(data)'></Slider>
              </div>
              <div class="popover-container popover-emailCode" v-else>
                <div class="popover-close" @click="showPopper = false">+</div>
                <div class="popover-title-top">
                  {{popoverTitle}}
                </div>
                <el-input
                  placeholder=""
                  v-model="RegisterFrom.emailCode"
                  clearable>
                </el-input>
                <div class="rotate-code-button">
                  <el-button @click="Register" type="primary"></el-button>
                </div>
              </div>
              <!--      -->
              <el-button slot="reference" disabled type="primary"
                         style="width: 400px" v-if="showCountDownRotateCode === true">
                {{getRotateCodeBtnTxt+'('+countDown+')'}}
              </el-button>
              <el-button slot="reference" @click="getRotateCode" type="primary"
                         style="width: 400px" v-else>{{getRotateCodeBtnTxt}}
              </el-button>
            </el-popover>
          </el-form-item>
        </el-form>
        <router-link to="/Login" class="go-login"></router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      var passwordAgain = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.RegisterFrom.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        //
        rangeStatus: false,
        timer: null,
        // status: false,
        //
        countDown: 60,
        //
        showCountDownRotateCode: false,
        //
        getRotateCodeBtnTxt: '',
        //
        popoverTitle: '',
        //
        showRotateCode: true,
        //
        showPopper: false,
        //
        rotateAngle: 0,
        //base64
        rotateCodeUrl: '',
        RegisterFrom: {
          userName: '',
          password: '',
          passwordAgain: '',
          email: '',
          emailCode: ''
        },
        // 
        RegisterFromRules: {
          email: [
            {required: true, message: '', trigger: 'change'},
            {type: 'email', message: '', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'change'},
            {min: 7, message: '6', trigger: 'blur'},
            {max: 17, message: '18', trigger: 'blur'}
          ],
          userName: [
            {required: true, message: '', trigger: 'change'},
            {max: 8, message: '8', trigger: 'blur'}
          ],
          passwordAgain: [
            {validator: passwordAgain, trigger: 'change'}
          ],
          emailCode: [
            {required: true, message: '', trigger: 'change'}
          ],
        },
      }
    },
    methods: {
      //
      async getEmailCode() {
        this.rangeStatus = false;
        await this.$http.get('/api/user/emailCode', {
          email: this.RegisterFrom.email,
          angle: this.rotateAngle
        }).then((data) => {
          this.$message.success(data.message);
          this.rangeStatus = true;
          setTimeout(() => {
            this.showRotateCode = false;
            this.popoverTitle = '';
            this.showCountDownRotateCode = true;
            this.timer = setInterval(() => {
              if (this.countDown > 0) {
                this.countDown -= 1;
              } else {
                clearInterval(this.timer);
                this.getRotateCodeBtnTxt = '';
                this.showCountDownRotateCode = false;
              }
            }, 1000)
          }, 1000);
        }).catch(() => {
          this.rotateAngle = 0;
          this.rangeStatus = false;
          this.getRotateCode();
        })
      },
      //
      async getRotateCode() {
        this.rangeStatus = false;
        this.countDown = 60;
        this.popoverTitle = '';
        this.$refs.RegisterFromRef.validate(async (valid) => {
          if (valid) {
            await this.$http.get('/api/user/rotateCode', {
              email: this.RegisterFrom.email
            }).then((data) => {
              this.rotateCodeUrl = data.data;
              this.showPopper = true
            });
            this.rotateAngle = 0;
            this.showRotateCode = true
          }
        });
        this.RegisterFrom.emailCode = ''
      },
      //
      async Register() {
        if (this.RegisterFrom.emailCode === '') {
          this.$message.error('');
          return
        }

        await this.$http.post('/api/user/addByEmail', {
          email: this.RegisterFrom.email,
          password: this.RegisterFrom.password,
          name: this.RegisterFrom.userName,
          emailCode: this.RegisterFrom.emailCode
        }).then((data) => {
          this.$message.success(data.message);
          this.$router.push('./Login')
        })
      },
    },
  }
</script>

<style scoped>
  .title {
    margin-top: 80px;
    margin-bottom: 10px;
    text-align: center;
    color: white;
    font-size: 25px;
    font-weight: bold;
  }

  .content {
    margin: 0 auto;
    width: 850px;
    height: 100%;
    overflow: hidden;
  }

  .login_container {
    background-color: #FF7F50;
    height: 100%;
  }

  .login_box {
    width: 550px;
    height: 500px;
    background-color: #fff;
    border-radius: 20px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  .e_form {
    width: 400px;
    margin: 0 auto;
    padding-top: 20px;
  }

  .popover-container {
    text-align: center;
    height: 280px;
    padding: 20px;
    position: relative;
  }

  .popover-emailCode {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .popover-title {
    text-align: center;
    font-size: 14px;
  }

  .popover-title-top {
    font-size: 16px;
    margin-bottom: 30px;
    font-weight: bold;
  }

  .popover-close {
    transform: rotate(45deg);
    position: absolute;
    top: -13px;
    right: 0;
    font-size: 30px;
    cursor: pointer;
  }

  .go-login {
    color: #999;
    text-decoration: none;
    border-bottom: 1px solid #fff;
  }

  .go-login:hover {
    border-bottom: 1px solid #999;
  }
</style>
