<template>
  <el-container class="home-container" v-title data-title="  ">
    <el-header v-if="isMobile==false">
      <img src="../../assets/logo1.png" alt="" class="logo">
      <el-menu class="menu" :default-active="activeIndex" mode="horizontal" @select="handleSelect"
               text-color="#000"
               active-text-color="#fff"
               background-color="#FF7F50">
        <el-menu-item index="/TableList">
          <i class="ico fas fa-clipboard"></i>
          
        </el-menu-item>
        <el-menu-item index="/Confession">
          <i class="ico fas fa-heart"></i>
          
        </el-menu-item>
        <el-menu-item index="/Help">
          <i class="ico fas fa-question"></i>
          
        </el-menu-item>
      </el-menu>
      <div style="display: flex;align-items: center">
        <el-input v-model="SearchKeyword" @keyup.enter.native="goSearch" placeholder=""
                  style="width: 15rem;margin-right: 5.25rem">
          <el-button @click="goSearch" slot="append" icon="el-icon-search"></el-button>
        </el-input>

        <div v-if="isLogin" class="userAvatar" @mouseenter="show = !show" @mouseleave="show = !show">
          <!--      -->
          <div class="avatar_box">
            <img src="@/assets/img/avatar.jpg" alt="">
            <!--      -->
          </div>
          <div class="userInfo">
            <transition name="el-fade-in-linear">
              <div v-show="show" class="transition-box">
                <ul>
                  <li>
                    <span class="userName"></span>
                    {{ this.userInfo.name }}
                  </li>
                  <li>
                    <span></span>
                    <div class="text">
                      {{ this.userInfo.email }}
                    </div>
                  </li>
                  <li>
                    <span></span>
                    <div class="text">
                      {{ this.toDates(this.userInfo.createTime) }}
                    </div>
                  </li>
                  <li>
                    <span></span>
                    <div class="text">
                      {{ this.toDates(this.userInfo.lastTime) }}
                    </div>
                  </li>
                </ul>
              </div>
            </transition>
          </div>
        </div>
        <!--  -->
        <el-button v-if="isLogin" class="login" type="primary" round size="small" @click="Logout"></el-button>
        <!--  -->
        <el-button v-else class="login" type="primary" round size="small" @click="Login"></el-button>
      </div>

    </el-header>

    <el-container>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  export default {
    data() {
      return {
        userInfo: null,
        show: false,
        //
        isMobile: false,
        activeIndex: '1',
        // 
        SearchKeyword: '',
        //
        changeMenuState: false,
        isLogin: false
      }
    },
    created() {
      //
      this.isMobile = this.getIsMobile();
      //
      this.activeIndex = this.$route.path;
      // 
      this.getUserInfo()
    },
    watch: {
      userInfo(newV, oldV) {
        this.isLogin = !this.isNull(newV)
      }
    },
    methods: {
      Login() {
        this.$router.push('/Login');
      },
      Logout() {
        this.$http.post('/api/user/logout').then((response) => {
          this.setToken('')
          this.$message.success('');
          this.$router.push('/Login');
        })
      },
      //
      goSearch() {
        this.$router.push({
          name: 'Search',
          params: {keyword: this.SearchKeyword}
        })
      },
      //
      handleSelect(key, keyPath) {
        this.$router.push({
          path: key
        })
      },
      //
      changeMenuState() {
        this.isMenuState = !this.isMenuState
      },
      //
      async getUserInfo() {

        //token
        if (this.isNull(this.getToken())) {
          this.userInfo = null
          return
        }

        await this.$http.get('/api/user/user', {
          keyword: this.keyword,
          page_index: this.page_index,
          page_size: this.page_size
        }).then((data) => {
          this.userInfo = data.data
        }).catch((res) => {
          this.userInfo = null
        })
      }
    }
  }
</script>
<style scoped lang="less">

  .logo {
    height: 60px;
  }

  .el-header {
    width: 1300px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
    font-size: 1.25rem;
    position: relative;
    background-color: #FF7F50;
  }

  .el-input {
    margin-right: 100px;
  }

  .transition-box {
    width: 230px;
    height: 300px;
    background-color: #fff;
    position: absolute;
    top: 30px;
    right: -83px;
    border-radius: 10px;
    font-size: 16px;
    color: #555;
    overflow: hidden;
    z-index: 998;
    box-shadow: 5px 5px 10px #999;
    border: 1px solid #e4e4e4;

    ul {
      margin-top: 30px;

      li {
        width: 100%;
        border-bottom: 1px solid #e3e3e3;
        padding: 10px 10px 22px 10px;

        span {
          display: block;
          color: #4d6bff;
        }

        .text {
          padding-left: 20px;
          font-size: 14px;
          /*color: rgb(112, 116, 116);*/
          padding-top: 10px;
          text-align: center;
        }

        .userName {
          display: inline-block;

        }
      }

      li:last-child {
        border: 0;
      }
    }
  }

  .el-button {
    margin-left: 20px;
  }

  .userAvatar {
    position: relative;
  }

  .userAvatar:hover .avatar_box {
    height: 80px;
    width: 80px;
    top: -25px;
    right: -10px;
  }

  .avatar_box {
    height: 50px;
    width: 50px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 2px;
    box-shadow: 0 0 10px #999;
    background-color: #fff;
    cursor: pointer;
    z-index: 999;
    position: absolute;
    top: -25px;
    right: 10px;
    transition: .3s all;
  }

  .avatar_box img {
    width: 100%;
    border-radius: 50%;
  }

  .home-container {
    background-color: #FF7F50;
    height: 100%;
    margin-right: calc(100% - 100vw);
  }

  .item, .el-header {
    font-size: 16px;
  }

  .el-aside {
    background-color: #82ADD9;
  }

  .el-main {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f7fa;
  }

  .menu i {
    color: #000;
  }

  .login {
    margin-right: 0.25rem
  }
</style>
